* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html {
  height: 100%;
  font-family: 'Poppins', sans-serif;
}

.login-container {
  display: flex;
  height: 100vh;
  background-color: #1e1e2e; /* Dark background for the whole container */
  overflow: hidden;
}

.left-section {
  position: relative;
  width: 45%;
  overflow: hidden;
  background-color: black;
}

.background-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: white;
  padding: 20px;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 8px;
}

.overlay h2 {
  font-size: 28px;
  font-weight: bold;
  margin-bottom: 10px;
}

.overlay p {
  font-size: 16px;
  line-height: 1.5;
}

.right-section {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #2e2e3e;
  padding: 60px;
  overflow-y: auto;
}

.form-card {
  width: 100%;
  max-width: 480px;
  padding: 40px;
  background-color: #1e1e2e;
  border-radius: 16px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.5);
}

.back-button {
  align-self: flex-start;
  color: #f8f9fa;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 600;
  margin-bottom: 20px;
}

.form-header {
  font-size: 32px;
  font-weight: bold;
  color: #f8f9fa;
  margin-bottom: 10px;
}

.form-subheader {
  font-size: 16px;
  color: #b0b0b0;
  margin-bottom: 20px;
}

.signup-form .form-group {
  margin-bottom: 20px;
  width: 100%; /* Ensure full width for input fields */
}

.form-row {
  display: flex;
  gap: 20px;
}

.signup-form input {
  width: 100%; /* Make input fields take full width */
  padding: 14px;
  font-size: 16px;
  border: 1px solid #495057;
  border-radius: 8px;
  background-color: #343a40;
  color: #f8f9fa;
}

.signup-form input::placeholder {
  color: #b0b0b0;
}

.signup-form input:focus {
  border-color: #6c757d;
  outline: none;
}

.signup-button {
  width: 100%; /* Full width for the signup button */
  padding: 14px;
  background-color: #6f42c1;
  color: #f8f9fa;
  font-size: 18px;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 20px;
}

.signup-button:hover {
  background-color: #5936b2;
}

.password-group {
  display: flex;
  align-items: center;
  position: relative;
}

.eye-icon {
  position: absolute;
  right: 16px;
  cursor: pointer;
  color: #b0b0b0;
}

.social-buttons {
  display: flex;
  justify-content: center;
  gap: 10px;
}

.google-button,
.apple-button {
  flex: 1;
  padding: 12px;
  font-size: 16px;
  border-radius: 8px;
  border: 1px solid #495057;
  background-color: #2e2e3e;
  color: #f8f9fa;
  cursor: pointer;
}

.google-button:hover,
.apple-button:hover {
  background-color: #343a40;
}

.login-link {
  color: #6f42c1;
  font-weight: 600;
}

.terms-link {
  color: #6f42c1;
  text-decoration: none;
}

.terms-link:hover {
  text-decoration: underline;
}

/* Remove the white/yellow autofill background */
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
textarea:-webkit-autofill,
textarea:-webkit-autofill:hover,
textarea:-webkit-autofill:focus,
select:-webkit-autofill,
select:-webkit-autofill:hover,
select:-webkit-autofill:focus {
  border: 1px solid #495057;
  -webkit-text-fill-color: #f8f9fa;
  -webkit-box-shadow: 0 0 0px 1000px #343a40 inset;
  transition: background-color 5000s ease-in-out 0s;
}

/* Media Queries for smaller screens */
@media (max-width: 768px) {
  .login-container {
    flex-direction: column;
  }

  .left-section {
    width: 100%;
    height: 40vh; /* Adjusted to keep some space for content */
  }

  .right-section {
    padding: 20px;
    justify-content: flex-start;
    align-items: flex-start;
  }

  .form-card {
    width: 100%;
    max-width: 100%;
    padding: 20px;
  }

  .form-header {
    font-size: 28px;
  }

  .form-subheader {
    font-size: 14px;
  }

  .signup-button {
    font-size: 16px;
  }

  .social-buttons {
    flex-direction: column;
    gap: 10px;
  }

  .google-button,
  .apple-button {
    width: 100%;
    padding: 12px;
  }
}

@media (max-width: 480px) {
  .login-container {
    overflow-x: hidden;
    height: 100%;
  }

  .left-section {
    height: 35vh;
  }

  .overlay h2 {
    font-size: 22px;
  }

  .overlay p {
    font-size: 14px;
  }

  .form-header {
    font-size: 24px;
  }

  .form-subheader {
    font-size: 12px;
  }

  .form-card {
    width: 100%;
    padding: 10px;
  }

  .signup-button {
    padding: 12px;
    font-size: 14px;
  }

  .password-group input {
    padding: 12px;
  }

  .social-buttons {
    gap: 5px;
  }

  .google-button,
  .apple-button {
    padding: 10px;
    font-size: 14px;
  }

  .back-button {
    font-size: 12px;
  }
}
